import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { NavBar, Tag } from 'njui-mobile';

class TagPage extends Component {
    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='Tag' fixed onLeftClick={() => window.history.go(-1)} />

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Tag></Tag>
                        <Tag type='danger'></Tag>
                        <Tag type='success'></Tag>
                        <Tag type='primary'></Tag>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Tag plain></Tag>
                        <Tag plain type='danger'></Tag>
                        <Tag plain type='success'></Tag>
                        <Tag plain type='primary'></Tag>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Tag mark></Tag>
                        <Tag mark type='danger'></Tag>
                        <Tag mark type='success'></Tag>
                        <Tag mark type='primary'></Tag>
                    </div>
                </div>
            </div>
        );
    }
}

TagPage.propTypes = {};
TagPage.defaultProps = {};

export default TagPage;



