import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { Button, NavBar } from 'njui-mobile';

class ButtonPage extends Component {
    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='Button' fixed onLeftClick={() => window.history.go(-1)} />
                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button></Button>
                        <Button type='primary'></Button>
                    </div>
                    <div className='demo-block__row'>
                        <Button type='warning'></Button>
                        <Button type='danger'></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain></Button>
                        <Button plain type='primary'></Button>
                    </div>
                    <div className='demo-block__row'>
                        <Button plain type='warning'></Button>
                        <Button plain type='danger'></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button size='large'></Button>
                    </div>
                    <div className='demo-block__row'>
                        <Button size='normal'></Button>
                        <Button size='small'></Button>
                        <Button size='mini'></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button disabled></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button onClick={() => window.alert('You clicked me!')}>Click</Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button loading></Button>
                    </div>
                </div>
            </div>
        );
    }
}

ButtonPage.propTypes = {};
ButtonPage.defaultProps = {};

export default ButtonPage;



