import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { Cell, NavBar } from 'njui-mobile';

const CellGroup = Cell.Group;

class CellPage extends Component {
    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='Cell' fixed onLeftClick={() => window.history.go(-1)} />
                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <CellGroup>
                            <Cell title='' value='' />
                            <Cell title='' value='' label='' />
                        </CellGroup>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'> value</div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <CellGroup>
                            <Cell value='' />
                        </CellGroup>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <CellGroup>
                            <Cell title='' value='' icon='location' />
                        </CellGroup>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <CellGroup>
                            <Cell title='' value='' arrow-direction='up' />
                            <Cell title='' value='' arrow-direction='right' />
                            <Cell title='' value='' arrow-direction='down' />
                            <Cell title='' value='' arrow-direction='left' />
                        </CellGroup>
                    </div>
                </div>
            </div>
        );
    }
}

CellPage.propTypes = {};
CellPage.defaultProps = {};

export default CellPage;



