import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types'; // eslint-disable-line
import { Icon } from 'njui-mobile';
import logoUrl from '../../assets/logo.png';

class HomePage extends Component {
    constructor(props) {
        super(props);

        this.state = {
            menus: [
                {
                    label: '',
                    open: false,
                    children: [
                        {
                            label: 'Layout ',
                            path: '/layout'
                        },
                        {
                            label: 'Badge ',
                            path: '/badge'
                        },
                        {
                            label: 'Button ',
                            path: '/button'
                        },
                        {
                            label: 'Cell ',
                            path: '/cell'
                        },
                        {
                            label: 'Icon ',
                            path: '/icon'
                        },
                        {
                            label: 'Modal ',
                            path: '/modal'
                        },
                        {
                            label: 'NavBar ',
                            path: '/nav-bar'
                        },
                        {
                            label: 'Pagination ',
                            path: '/pagination'
                        },
                        {
                            label: 'Progress ',
                            path: '/progress'
                        },
                        {
                            label: 'Step ',
                            path: '/step'
                        },
                        {
                            label: 'Swipe ',
                            path: '/swipe'
                        },
                        {
                            label: 'Tab ',
                            path: '/tab'
                        },
                        {
                            label: 'Tag ',
                            path: '/tag'
                        },
                        {
                            label: 'TabBar ',
                            path: '/tab-bar'
                        }
                    ]
                },
                {
                    label: '',
                    open: false,
                    children: [
                        {
                            label: 'Checkbox ',
                            path: '/checkbox'
                        },
                        {
                            label: 'Field ',
                            path: '/checkbox'
                        },
                        {
                            label: 'NumberKeyboard ',
                            path: '/number-keyboard'
                        },
                        {
                            label: 'PasswordInput ',
                            path: '/password-input'
                        },
                        {
                            label: 'Radio ',
                            path: '/radio'
                        },
                        {
                            label: 'Rate ',
                            path: '/rate'
                        },
                        {
                            label: 'Search ',
                            path: '/search'
                        },
                        {
                            label: 'Slider ',
                            path: '/slider'
                        },
                        {
                            label: 'Stepper ',
                            path: '/stepper'
                        },
                        {
                            label: 'Switch ',
                            path: '/switch'
                        },
                        {
                            label: 'Uploader ',
                            path: '/uploader'
                        }
                    ]
                },
                {
                    label: '',
                    open: false,
                    children: [
                        {
                            label: 'ActionSheet ',
                            path: '/action-sheet'
                        },
                        {
                            label: 'DatetimePicker ',
                            path: '/datetime-picker'
                        },
                        {
                            label: 'Picker ',
                            path: '/picker'
                        },
                        {
                            label: 'PullRefresh ',
                            path: '/pull-refresh'
                        },
                        {
                            label: 'Toast ',
                            path: '/toast'
                        },
                        {
                            label: 'Modal ',
                            path: '/modal'
                        }
                    ]
                },
                {
                    label: '',
                    open: false,
                    badge: <b style={{ color: '#4CAF50', fontSize: '12px', marginLeft: '10px' }}>NEW!</b>,
                    children: [
                        {
                            label: 'Card ',
                            path: '/card'
                        }
                    ]
                }
            ]
        };
    }

    /**
     * Toggle Menu
     * @param index
     */
    toggleMenu = (index) => {
        const { menus } = this.state;
        const result = [];

        menus.forEach((item, i) => {
            if (index === i) {
                item.open = !item.open;
            }
            result.push(item);
        });
        this.setState({
            menus: result
        });
    };

    render() {
        const { menus } = this.state;
        return (
            <div className='page page__padding'>
                <div className='page__title'>
                    <div className='page__title--logo'>
                        <img src={logoUrl} alt='' />
                        <span>njui-mobile</span>
                    </div>
                    <div className='page__title--desc'> React </div>
                </div>
                <div className='page__body'>
                    {menus.map((menu, i) => {
                        return (
                            <div key={i} className='demo-list'>
                                <div className='demo-list__row'>
                                    <div className='demo-list__row--title' onClick={() => this.toggleMenu(i)}>
                                        <span>
                                            {menu.label}
                                            {menu.badge}
                                        </span>
                                        <Icon name='arrow' />
                                    </div>
                                    <div className='demo-list__row--body'>
                                        {menu.open && menu.children && menu.children.map((child, j) => {
                                            return (
                                                <Link to={child.path} key={j}>{child.label}</Link>
                                            );
                                        })}
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    }
}

HomePage.propTypes = {};
HomePage.defaultProps = {};

export default HomePage;



