import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { NavBar, Button, Modal } from 'njui-mobile';

class ModalPage extends Component {
    constructor(props) {
        super(props);

        this.state = {
            confirmVisible: false
        };
    }

    render() {
        const { confirmVisible } = this.state;
        return (
            <div className='page'>
                <NavBar left-text='' title='Modal' fixed onLeftClick={() => window.history.go(-1)} />

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain>Alert</Button>
                        <Button plain> Alert</Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain onClick={() => this.setState({ confirmVisible: true })}>Confirm</Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain></Button>
                    </div>
                </div>

                <Modal
                    visible={confirmVisible}
                    title=''
                    okText=''
                    onOk={() => this.setState({ confirmVisible: false })}
                    cancelText=''
                    onCancel={() => this.setState({ confirmVisible: false })}
                >
                    
                </Modal>
            </div>
        );
    }
}

ModalPage.propTypes = {};
ModalPage.defaultProps = {};

export default ModalPage;



