import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { Button, NavBar, Toast } from 'njui-mobile';

class ToastPage extends Component {
    showToast = () => {
        Toast.success('Hello World', 3);
    };

    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='Toast' fixed onLeftClick={() => window.history.go(-1)} />

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain onClick={() => Toast.show('~')}></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain>TODO</Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'>/</div>
                    <div className='demo-block__row'>
                        <Button plain onClick={() => Toast.success('')}></Button>
                        <Button plain onClick={() => Toast.fail('')}></Button>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row'>
                        <Button plain onClick={() => Toast.show('10', 10)}></Button>
                    </div>
                </div>
            </div>
        );
    }
}

ToastPage.propTypes = {};
ToastPage.defaultProps = {};

export default ToastPage;



