import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { NavBar, TabBar } from 'njui-mobile';

class TabBarPage extends Component {
    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='TabBar' fixed onLeftClick={() => window.history.go(-1)} />

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div
                        className='demo-block__row'
                        style={{ marginLeft: '-15px', marginRight: '-15px' }}
                    >
                        <TabBar>
                            <TabBar.Item active icon='home'>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='chat' dot onClick={() => window.alert('You clicked me!')}>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='records' info='10'>
                                
                            </TabBar.Item>
                            <TabBar.Item
                                value='contact'
                                icon='contact'
                                info='...'
                            >
                                
                            </TabBar.Item>
                        </TabBar>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div
                        className='demo-block__row'
                        style={{ marginLeft: '-15px', marginRight: '-15px' }}
                    >
                        <TabBar>
                            <TabBar.Item active icon='home'>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='chat' onClick={() => window.alert('You clicked me!')}>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='records'>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='contact'>
                                
                            </TabBar.Item>
                        </TabBar>
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div
                        className='demo-block__row'
                        style={{ marginLeft: '-15px', marginRight: '-15px' }}
                    >
                        <div style={{ padding: '0 15px' }}></div>
                        <TabBar fixed>
                            <TabBar.Item active icon='home'>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='chat' dot>
                                
                            </TabBar.Item>
                            <TabBar.Item icon='records' info='10'>
                                
                            </TabBar.Item>
                            <TabBar.Item
                                icon='contact'
                                info='...'
                            >
                                
                            </TabBar.Item>
                        </TabBar>
                    </div>
                </div>
            </div>
        );
    }
}

TabBarPage.propTypes = {};
TabBarPage.defaultProps = {};

export default TabBarPage;



