import React, { Component } from 'react';
import PropTypes from 'prop-types'; // eslint-disable-line
import { NavBar } from 'njui-mobile';

class NavBarPage extends Component {
    render() {
        return (
            <div className='page'>
                <NavBar left-text='' title='NavBar' fixed onLeftClick={() => window.history.go(-1)} />

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <NavBar
                            left-arrow
                            left-text=''
                            title=''
                            right-text=''
                            onLeftClick={() => window.alert('Click Left')}
                            onRightClick={() => window.alert('Click Right')}
                        />
                    </div>
                </div>

                <div className='demo-block'>
                    <div className='demo-block__title'></div>
                    <div className='demo-block__row' style={{ marginLeft: '-15px', marginRight: '-15px' }}>
                        <NavBar left-arrow left-text='' title='' right-text='' right-icon='search' />
                    </div>
                </div>
            </div>
        );
    }
}

NavBarPage.propTypes = {};
NavBarPage.defaultProps = {};

export default NavBarPage;



